.job-entry {
  position: relative;
}

.job-entry>h3 {
  position: absolute;
  left: -24px;
  top: -11px;
}

.job-entry .workpanel-marker {
  margin-bottom: 20px;
}

.workpanel-marker {
  background-color: black;
  display: block;
  border-radius: .2rem;
  width: 40px;
  height: 40px;
  margin-left: 55px;
}

.workpanel-marker-hudsonrivertrading {
  background: -webkit-gradient( linear, left bottom, left top, from(#f7f7f7), color-stop(50%, #f7f7f7), color-stop(50%, #f79517), to(#f79517));
  background: -webkit-linear-gradient( bottom, #f7f7f7 0%, #f7f7f7 50%, #f79517 50%, #f79517 100%);
  background: -o-linear-gradient( bottom, #f7f7f7 0%, #f7f7f7 50%, #f79517 50%, #f79517 100%);
  background: linear-gradient( 0deg, #f7f7f7 0%, #f7f7f7 50%, #f79517 50%, #f79517 100%);
}

.workpanel-marker-facebook {
  background: -webkit-gradient( linear, left bottom, left top, from(white), color-stop(50%, white), color-stop(50%, #3b5998), to(#3b5998));
  background: -webkit-linear-gradient( bottom, white 0%, white 50%, #3b5998 50%, #3b5998 100%);
  background: -o-linear-gradient( bottom, white 0%, white 50%, #3b5998 50%, #3b5998 100%);
  background: linear-gradient( 0deg, white 0%, white 50%, #3b5998 50%, #3b5998 100%);
}

.workpanel-marker-google {
  background: -webkit-gradient( linear, left bottom, left top, from(#4285f4), color-stop(25%, #4285f4), color-stop(25%, #34a853), color-stop(50%, #34a853), color-stop(50%, #fbbc05), color-stop(75%, #fbbc05), color-stop(75%, #ea4335), to(#ea4335));
  background: -webkit-linear-gradient( bottom, #4285f4 0%, #4285f4 25%, #34a853 25%, #34a853 50%, #fbbc05 50%, #fbbc05 75%, #ea4335 75%, #ea4335 100%);
  background: -o-linear-gradient( bottom, #4285f4 0%, #4285f4 25%, #34a853 25%, #34a853 50%, #fbbc05 50%, #fbbc05 75%, #ea4335 75%, #ea4335 100%);
  background: linear-gradient( 0deg, #4285f4 0%, #4285f4 25%, #34a853 25%, #34a853 50%, #fbbc05 50%, #fbbc05 75%, #ea4335 75%, #ea4335 100%);
}

.workpanel-marker-lyft {
  background: -webkit-gradient( linear, left bottom, left top, from(#f3f3f5), color-stop(50%, #f3f3f5), color-stop(50%, #ff00bf), to(#ff00bf));
  background: -webkit-linear-gradient( bottom, #f3f3f5 0%, #f3f3f5 50%, #ff00bf 50%, #ff00bf 100%);
  background: -o-linear-gradient( bottom, #f3f3f5 0%, #f3f3f5 50%, #ff00bf 50%, #ff00bf 100%);
  background: linear-gradient( 0deg, #f3f3f5 0%, #f3f3f5 50%, #ff00bf 50%, #ff00bf 100%);
}

.workpanel-marker-amazon {
  background: -webkit-gradient( linear, left bottom, left top, from(#fefefe), color-stop(33%, #fefefe), color-stop(33%, #ff9900), color-stop(66%, #ff9900), color-stop(66%, #131A22), to(#131A22));
  background: -webkit-linear-gradient( bottom, #fefefe 0%, #fefefe 33%, #ff9900 33%, #ff9900 66%, #131A22 66%, #131A22 100%);
  background: -o-linear-gradient( bottom, #fefefe 0%, #fefefe 33%, #ff9900 33%, #ff9900 66%, #131A22 66%, #131A22 100%);
  background: linear-gradient( 0deg, #fefefe 0%, #fefefe 33%, #ff9900 33%, #ff9900 66%, #131A22 66%, #131A22 100%);
}

.workpanel-marker-internblitz {
  background: -webkit-gradient( linear, left bottom, left top, from(#FFFFFF), color-stop(50%, #FFFFFF), color-stop(50%, #E74C3C), color-stop(50%, #E74C3C));
  background: -webkit-linear-gradient( bottom, #FFFFFF 0%, #FFFFFF 50%, #E74C3C 50%, #E74C3C 50%);
  background: -o-linear-gradient( bottom, #FFFFFF 0%, #FFFFFF 50%, #E74C3C 50%, #E74C3C 50%);
  background: linear-gradient( 0deg, #FFFFFF 0%, #FFFFFF 50%, #E74C3C 50%, #E74C3C 50%);
}

.workpanel-marker-vertafore {
  background: -webkit-gradient( linear, left bottom, left top, from(#f95203), color-stop(50%, #f95203), color-stop(50%, #fefefe), to(#fefefe));
  background: -webkit-linear-gradient( bottom, #f95203 0%, #f95203 50%, #fefefe 50%, #fefefe 100%);
  background: -o-linear-gradient( bottom, #f95203 0%, #f95203 50%, #fefefe 50%, #fefefe 100%);
  background: linear-gradient( 0deg, #f95203 0%, #f95203 50%, #fefefe 50%, #fefefe 100%);
}

.workpanel-marker-tech {
  background: -webkit-gradient( linear, left bottom, left top, from(#FFFFFF), color-stop(50%, #FFFFFF), color-stop(50%, #EAAA00), to(#EAAA00));
  background: -webkit-linear-gradient( bottom, #FFFFFF 0%, #FFFFFF 50%, #EAAA00 50%, #EAAA00 100%);
  background: -o-linear-gradient( bottom, #FFFFFF 0%, #FFFFFF 50%, #EAAA00 50%, #EAAA00 100%);
  background: linear-gradient( 0deg, #FFFFFF 0%, #FFFFFF 50%, #EAAA00 50%, #EAAA00 100%);
}

.workpanels .timeline-entry {
  background-color: #f4f4f4;
  display: block;
  border-radius: .2rem;
  left: 107px;
  padding: 10px;
  position: relative;
  top: -60px;
  width: calc(100% - 150px);
}

.timeline-entry-title>h4 {
  font-family: 'Ubuntu Mono', monospace;
  display: inline;
}


